.header {
  display: flex;
  align-items: center;
  height: 100px;

  &__burger {
    display: none;
  }
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  &__list {
    display: flex;
    gap: 20px;

    font: normal normal 300 12px/14px Montserrat;
    letter-spacing: 4.8px;
    color: #afafaf;
    text-transform: uppercase;
    opacity: 1;
  }

  &__social {
    display: flex;
    gap: 11.9px;
  }

  &__link {
    padding: 21px;
  }
}
